.about__container{
    box-shadow:0 13px 27px -5px rgba(50, 50, 93, .25), 0 9px 16px -8px rgba(0, 0, 0, .3),0 -6px 16px -6px rgba(0, 0, 0, .025);
    padding:20px;
    border-radius:5px;
    background:#ffffff;
    margin:100px auto 0;
    max-width:400px;
    font-weight:300;
    line-height:1.8;
}
.about__container span:first-child{
    font-size:20px;
}
.about__container span:last-child{
    display:block;
    margin-top:10px;
}